@import 'assets/styles/index.scss';

.operationList {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 8px;

  margin-left: auto;
  margin-right: auto;

  width: 100%;
  max-width: 450px;
}

.loader {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.operationItem {
  height: 329px;
  border-radius: 10px;
  overflow: hidden;

  background-color: #dcdcdf;
}

.operationItemUl {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.operationInfo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px 7px 15px;
  vertical-align: middle;

  background-color: var(--background-white-color);
}

.operationInfoIncome {
  border-left: 5px solid #24cca7;
}

.operationInfoExpence {
  border-left: 5px solid #ff6596;
}

.operationInfo:last-child {
  padding: 10px 15px 6px 15px;
}

.mobileTableHeader {
  font-family: 'Circe';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.5;
}

.mobileTableInfo {
  font-family: 'Circe';
  font-style: normal;
  font-size: 16px;
  line-height: 1.5;
  text-align: right;
}

.category {
  display: flex;
  gap: 20px;
  height: 46px;
}

.mobileTableInfoCategory {
  overflow: auto;

  &::-webkit-scrollbar {
    height: 2px;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  }
}

.mobileTableInfoIncome {
  font-weight: 700;
  color: #24cca7;
}

.mobileTableInfoExpence {
  font-weight: 700;
  color: #ff6596;
}

.mobileDeleteWrapper {
  padding: 12px;
}

.mobileDelete {
  background-color: transparent;
  border: none;
}
